<template>
   <el-row class="examPC">
     <el-col :span="24" class="top">
   	  <el-col :span="2">
   	  		<img src="../../assets/img/tag.png" >
   				<span class="ctitle">
   					我的考试
   				</span>
   	  </el-col>
   	<!--  <el-col :span="2" :offset="20" class="more">
   	  		更多	  
   	  </el-col> -->
     </el-col>
	 <el-col class="searchCommon">
	 		 <span class="vimg spanCo">
	 			  <img src="../../assets/img/time.png" >
	 		 </span>
	 		 <span class="imgTitle spanCo">
	 			时间排序
	 		 </span>
	 		 <span class="vimg spanCo" @click="sort()">
	 		 	  <img src="../../assets/img/px.png" >
	 		 </span>
	 		 <span class="imgTitle spanCo">试卷名称</span>
	 		 <el-col :span="4" class="imput">
	 		 <el-input placeholder="输入相关试卷名称" v-model="input">
	 		  </el-input>
	 		 </el-col>
	 		 <el-button type="primary"  @click="search()"><img src="../../assets/img/search.png" >搜索</el-button>
	 </el-col>
	 <el-col :span="24" class="bottom">
	
		 <div class="bbottom">
		 	<div class="comBox" v-for="(item,index) in listData" :key="index">
		 		<div class="comTop">
		 			
					<div class="leftBtnText">
						{{item.name}}
					</div>
					<div class="rightText">
						发布时间:{{item.createTime}}
					</div>
		 		</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		发布人:<span class="bigText">{{item.createUser}}</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">{{item.serial}}</span>
					</div>
					<div class="">
						时长:<span class="bigText">{{item.time}}分钟</span>
					</div>
					<div class="">
						考试时间:<span class="bigText">{{item.startTime}}</span>
					</div>
					<div class="">
						分类:<span class="bigText">{{item.classify}}</span>
					</div>
				 </div>
				 <div class="startBtn" @click="startT(item)" >
				 	开始考试
				 </div>
				 <div class="startBtn comBtn" v-show="item.answeredTimes>100">
				 	已完成
				 </div>
		 	</div>
			<div class="comBox" v-show="false">
				<div class="comTop">
				
					<div class="leftBtnText">
						职业与创业胜任力胜任力…
					</div>
					<div class="rightText">
						发布时间:2021-09-08-12 12:12:23
					</div>
				</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		主讲人:<span class="bigText">张朝阳</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">01209131121</span>
					</div>
					<div class="">
						时长:<span class="bigText">40分钟</span>
					</div>
					<div class="">
						推演时间:<span class="bigText">2021-09-23-12 09:03:22</span>
					</div>
					<div class="">
						分类:<span class="bigText">理论</span>
					</div>
				 </div>
				 <div class="startBtn">
				 	开始考试
				 </div>
			</div>
			<div class="comBox" v-show="false">
				<div class="comTop">
					
					<div class="leftBtnText">
						职业与创业胜任力胜任力…
					</div>
					<div class="rightText">
						发布时间:2021-09-08-12 12:12:23
					</div>
				</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		主讲人:<span class="bigText">张朝阳</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">01209131121</span>
					</div>
					<div class="">
						时长:<span class="bigText">40分钟</span>
					</div>
					<div class="">
						推演时间:<span class="bigText">2021-09-23-12 09:03:22</span>
					</div>
					<div class="">
						分类:<span class="bigText">理论</span>
					</div>
				 </div>
				 <div class="startBtn">
				 	开始考试
				 </div>
			</div>
		 </div>
		 
	 </el-col>
	 <el-col :span="24" class="cpage">
	 			 <el-pagination
	 			   background
				   :prev-text='prevT'
				   :next-text='nextT'
				   :current-page.sync='cpage'
	 			   layout="prev, pager, next"
				   @current-change="current"
	 			   :total="total">
	 			 </el-pagination>
	 </el-col>
   </el-row>
</template>

<script>
	// import {getEaxm}  from '../../api/apis/user.js'
	import {examList} from '@/api/test/user.js'
export default {
	name:'examPC',
	data(){
		return {
			prevT:'上一页',
			nextT:'下一页',
			status:'进行中',
			status1:'',
			status2:'已完成',
			btns:[],
			oldIndex:0,
			nowIndex:0,
			input:'',
			listData:[],
			isAnserd:false,
			cpage:0,
			total:0,
			isId:'',
			orderByCreateTimeAsc:0
		}
	},
	mounted() {
		let that=this
		// getEaxm({name:this.input,limit:10,page:1,orderByCreateTimeAsc:this.orderByCreateTimeAsc}).then(res=>{
		// 	that.listData=res.data.data
		// 	that.total=res.data.totalCount
		// })

		examList({current:1,size:10}).then(res=>{
			that.listData=res.data.data
			that.total=res.data.total
		})
		// this.isId=JSON.parse(localStorage.getItem('isSen'))
	},
	methods:{
		select(item,index){
			this.nowIndex=index
		},
		startT(item){
			this.$router.push({path:'/student/sec/examSec',query:{id:item.id,time:item.time}})
		},
		current(val){
			let that=this
			// getEaxm({name:this.input,page:val,limit:10,orderByCreateTimeAsc:this.orderByCreateTimeAsc}).then(res=>{
			// 	// console.log(res)
			// 	that.listData=res.data.data
			// 	that.total=res.data.totalCount
			// })
			examList({current:val,size:10}).then(res=>{
			that.listData=res.data.data
			that.total=res.data.total
		})
		},
		search(){
			let that=this
			this.cpage=0
			getEaxm({name:this.input,limit:10,page:0,orderByCreateTimeAsc:this.orderByCreateTimeAsc}).then(res=>{
				// console.log(res)
				that.listData=res.data.data
				that.total=res.data.totalCount
			})
		},
		sort(){
			
			let that=this
			if(this.orderByCreateTimeAsc==0){
				this.orderByCreateTimeAsc=1
			}else {
				this.orderByCreateTimeAsc=0
			}
			getEaxm({name:this.input,limit:10,page:0,orderByCreateTimeAsc:this.orderByCreateTimeAsc}).then(res=>{
				// console.log(res)
				that.listData=res.data.data
				that.total=res.data.totalCount
			})
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/examPC.css';
</style>



